@mixin center-blocks-position {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}

@mixin minut-blocks-position {
  position: absolute;
  top: ($sizeWatch - $minutHeigh) / 4;
  left: ($sizeWatch - $minutWidth) / 2;
}

@mixin second-blocks-position {
  position: relative;
  top: ($sizeWatch - $secondheigh ) / 20;
  left: ($sizeWatch - $secondwidth) / 2;
  z-index: 1;
}
